.post{
  margin: 20px 0px;
}
.post:last-child{
  margin-bottom: 60px;
}

.header{
  padding:16px;
}
.user{
  display: flex;
  align-items: center;
}
.user img{
  object-fit: cover;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right:10px;

}
.user span{
  font-weight: 600;
}
/*https://css-tricks.com/aspect-ratio-boxes/*/
.content{
  position: relative; 
  padding-top: 100%;/*padding-top is based on the parent element's width*/
}
.imgContainer{
  position: absolute;
  top: 0; left: 0;
  height: 100%;
  width: 100%;
}

.content img{
  width:100%;
  object-fit:cover;
  height:100%;
}
.actions{
  display: flex;
  align-items: center;
  padding: 6px 16px;
  
}
.actions > button{
  margin-right:10px;
  display: flex; /* to remove empty space after image*/
}
.activity{
  padding: 0px 16px;
}

.likes{
  font-weight: 600;
  padding: 4px 0px;
}
.time{
  color: #999;
  font-size: 11px;
  padding: 4px 0px;
  
}
.comments{
  padding: 4px 0px;

}
.comments > div{
  display: flex;
  line-height: 1.5;
}
.comments span{
  margin-right: 4px;
}
.comments span:first-child{
  display: flex;
  font-weight: 600;
}
.addComment{
  margin-top: 6px;
  border-bottom: 1px solid #efefef;
  padding: 10px 0;
  display: flex;
}
.addComment input{
  background: none;
  border: none;
  height: 16px;
  outline: none;
  font:inherit;
  padding: 0;
  width: 100%;
}
.addComment ::placeholder { 
  color: #999;
}
.addComment button{
  color:#3897f0;
  font-weight: 600;
  font-size: 16px;
}
